
import {
  Box,
  Heading,
  Container,
  Text,
  Stack,
   Image,
} from '@chakra-ui/react';
// import Profile from "../images/Profile.webp"
export default function HomeSection() {
  return (
    <>
        <Container maxW={'3xl'}>
        <Stack
          as={Box}
          textAlign={'center'}
          spacing={{ base: 8, md: 14 }}
          py={{ base: 20, md: 36 }}>
           <Image boxSize='200px' src={"https://media.licdn.com/dms/image/D4D03AQF5JWyUybcevg/profile-displayphoto-shrink_400_400/0/1672484406082?e=1677715200&v=beta&t=giZeRANLQHGQGqbixwrdQlm2ulaq-GenL9ZPA4xuUUM"} alt='Kamal Gupta' />
          <Heading
            fontWeight={600}
            fontSize={{ base: '2xl', sm: '4xl', md: '6xl' }}
            lineHeight={'110%'}>
            Kamal Gupta <br />
            <Text as={'span'} color={'green.400'}>
            Full Stack Web Developer
            </Text>
          </Heading>
       </Stack>
      </Container>
    </>
  );
}

